				"html": "<ul>\n<li>Linux now operates on the majority of smartphones and tablets around the world via the <a href=\"https://en.wikipedia.org/wiki/Android_(operating_system)\">Android</a> OS (based off the Linux kernel). As of March 2016 Android holds <a href=\"https://www.netmarketshare.com/operating-system-market-share.aspx?qprid=8&#x26;qpcustomd=1\">60.99%</a> of the market share on mobile devices (tablets and smartphones). It also holds <a href=\"http://www.idc.com/prodserv/smartphone-os-market-share.jsp\">82.8%</a> for smartphones alone (per 2015 Q2 numbers).</li>\n<li>As of March 2016, Alexa reported that <a href=\"http://www.w3cook.com/os/summary/\">96.55%</a> of the top 1 million Internet domains in the world are powered by servers running Linux.</li>\n<li>The <a href=\"https://en.wikipedia.org/wiki/New_York_Stock_Exchange\">New York Stock Exchange (NYSE)</a>, the largest stock exchange in the world which trades securities worth billions of dollars daily are executed by servers that run on Linux after moving away from UNIX.</li>\n<li>The White House powers their web servers using <a href=\"https://en.wikipedia.org/wiki/Red_Hat\">Red Hat Linux</a> and <a href=\"https://en.wikipedia.org/wiki/Apache_HTTP_Server\">Apache</a>.</li>\n<li>The US Department of Defense (DoD) uses Linux to run their nuclear submarine fleet.</li>\n<li>The US Navy uses Linux for ground control software.</li>\n</ul>\n<p>Other adoptions of Linux from around the world can be viewed <a href=\"https://en.wikipedia.org/wiki/List_of_Linux_adopters\">here</a> and <a href=\"http://www.comparebusinessproducts.com/fyi/50-places-linux-running-you-might-not-expect\">here</a></p>",
